import * as React from 'react';
import { LineChart } from '@mui/x-charts/LineChart';

export default function BasicLineChart({data}) {
    console.log(data)
    let times = data.map(item => formatDate(item.time));
    let values = data.map(item => item.humi);
    return (
        <LineChart
          width={1900}
          height={900}
          series={[
            { data: values, label: ' m' },
          ]}
          xAxis={[{ scaleType: 'point', data: times  }]}
        />
      );
}



function formatDate(dateString) {
    const date = new Date(dateString);
    
    const day = date.getUTCDate();
    const month = date.getUTCMonth() + 1; // Thng 0-11, nn cng 1
    const year = date.getUTCFullYear();
    const hours = date.getUTCHours();
    const minutes = date.getUTCMinutes();
    const seconds = date.getSeconds()

    // nh dng li thnh chui
    return `${day}/${month}/${year} ${hours}:${minutes < 10 ? '0' : ''}${minutes}:${seconds}`;
}

